import React from "react";
import { graphql } from "gatsby";
import PostLink from "../components/blog/PostLink";
import { MainHeader } from "../helpers/header";
import { Footer } from "../helpers/footer";
import styled from "styled-components";
import { media } from "../helpers/media-queries";
import { SeoComponent } from "../helpers/seoComponent";

const BlogPage = ({
  data: {
    allMarkdownRemark: { edges },
  },
}) => {
  const Posts = edges
    .filter((edge) => !!edge.node.frontmatter.date) // You can filter your posts based on some criteria
    .map((edge) => <PostLink key={edge.node.id} post={edge.node} />);
  return (
    <SeoComponent title="Vosbor blog">
      <MainHeader dark></MainHeader>
      <BlogContainer>
        <BlogTitle>Blog</BlogTitle>
        {/* <BlogInfo>
          We drive access and transparency across agricultural commodity
          markets, enabling our users to capture more value. Interested to learn
          more?
        </BlogInfo> */}
        <PostContainer>{Posts}</PostContainer>
      </BlogContainer>
      <Footer></Footer>
    </SeoComponent>
  );
};
export default BlogPage;

// maxWidth: 820
// maxHeight: 440
// x4 -> 3280x1760
export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            slug
            title
            author
            bgcolor
            featuredImage {
              childImageSharp {
                fluid(maxWidth: 3280, maxHeight: 1760) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`;

export const BlogContainer = styled.div`
  margin-top: 101px;
  margin-left: 24px;
  margin-right: 24px;
  min-height: calc(100vh - 292px);

  ${media.m`
    margin-top: 112px;
    margin-left: 40px;
    margin-right: 40px;
  `}

  ${media.l`
    margin-top: 144px;
    margin-left: 81px;
    margin-right: 81px;
  `}

  ${media.xl`
    margin-top: 188px;
    margin-left: 100px;
    margin-right: 100px;
  `}
`;

const BlogTitle = styled.h1`
  font-family: "Poppins";
  font-size: 32px;
  font-weight: 500;
  line-height: 40px;
  color: #47474f;
  margin-bottom: 24px;
  ${media.xl`
    margin-bottom: 27px;
  `}
`;

const BlogInfo = styled.p`
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 400;
  line-height: 28px;
  margin-bottom: 30px;
  color: #747487;
`;

const PostContainer = styled.div`
  margin-bottom: 90px;

  ${media.m`
    display: grid; 
    grid-template-columns: 1fr 1fr;
    gap: 48px 24px;
  `}

  ${media.l`
    gap: 80px 85px;
    margin-bottom: 100px;
  `}

  ${media.xl`
    gap: 80px 120px;
    margin-bottom: 120px;
  `}
`;
